import { breakpoints } from "./_variables";
import { css } from "styled-components";

type MediaQuery = Record<
  keyof typeof breakpoints,
  (...args) => ReturnType<css>
>;

export const respondTo = Object.keys(breakpoints).reduce((acc, label) => {
  acc[label] = (...args) => css`
    @media screen and (max-width: ${breakpoints[label]}) {
      ${css(...args)}
    }
  `;
  return acc;
}, {}) as MediaQuery;
