import React from "react";
import styled from "styled-components";
import { Link } from "gatsby";
import { respondTo } from "./shared/_respondTo";
import BlogListItem from "./BlogListItem";

const BlogListContainer = styled.div`
  min-height: 600px;
  ${respondTo.md`
    width: 100%;
    max-width:100%;
  `}
`;

const BlogListContent = styled.div`
  display: flex;
  flex-direction: column;
  gap: 16px;

  ${respondTo.sm`
    grid-template-columns: 1fr;
    padding: 0 16px;
  `}
`;

const BlogPagination = styled.div`
  margin: 8px 0px;
`;

const PageNavLink = styled(Link)`
  margin: 0px 8px;
  :first-child {
    margin: 0;
    margin-right: 8px;
  }

  :last-child {
    margin: 0;
    margin-left: 8px;
  }
`;

export default function BlogListComponent(props: any) {
  const pagination = props.pagination;

  return (
    <BlogListContainer>
      <BlogListContent>
        {props.posts.map(({ node }: { node: any }) => {
          const title = node.frontmatter.title || "TBD";
          const date = node.frontmatter.date;
          const tags = node.frontmatter.tags;
          const desc = node.excerpt;

          return (
            <Link key={node.fields.slug} to={node.frontmatter.path}>
              <BlogListItem
                title={title}
                date={date}
                tags={tags}
                description={desc}
              />
            </Link>
          );
        })}
      </BlogListContent>
      <BlogPagination>
        {pagination
          ? pagination.numPages > 1
            ? Array(pagination.numPages)
                .fill(0)
                .map((_, idx) => (
                  <PageNavLink
                    key={idx + 1}
                    to={idx === 0 ? `/blog` : `/blog/${idx + 1}`}
                  >
                    {idx + 1}
                  </PageNavLink>
                ))
            : null
          : null}
      </BlogPagination>
    </BlogListContainer>
  );
}
