import { Link } from "gatsby";
import React from "react";
import styled from "styled-components";
import { respondTo } from "./shared/_respondTo";

const HeaderContainer = styled.header`
  ${respondTo.md`
    margin-bottom:0;
    padding: 0 16px;
  `}
`;

const HeaderContent = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 10vh;
  ${respondTo.xs`
    width:100%;
  `}

  a {
    color: var(--fontColor);
    text-decoration: none;
  }

  h1 {
    font-size: 28px;
  }

  ul {
    li {
      float: left;
      margin-right: 8px;
      &:last-child {
        margin: 0;
      }
    }
  }
`;

type Props = {
  siteTitle: string;
};

const Header = (props: Props) => {
  const { siteTitle } = props;

  return (
    <HeaderContainer>
      <HeaderContent>
        <div>
          <Link to="/">
            <h1>{siteTitle}</h1>
          </Link>
        </div>
        <div>
          <ul>
            <Link to="/post">
              <li>POST</li>
            </Link>
          </ul>
        </div>
      </HeaderContent>
    </HeaderContainer>
  );
};

export default Header;
